@import "./mixins.scss";

.favorite {
  padding: 40px 60px;
  .favorite-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 300px;
    .favorite-back {
      width: 40px;
      height: 40px;
      background: #ffffff;
      border: 1px solid #f2f2f2;
      color: #f2f2f2;
      border-radius: 5px;
      transition: color 0.3s ease-in;
      &:hover {
        color: #000000;
      }
    }
    h1 {
      @include biggesTitle;
      font-size: 32px;
      line-height: 39px;
    }
  }
  .favorite-content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 40px;
    gap: 40px;
    .favorite-empty {
      width: calc(100vw - 250px);
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img {
        width: 70px;
        height: 70px;
      }
      h3 {
        margin-top: 25px;
        font-family: "Inter";
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        text-align: center;
        color: #000000;
      }
      p {
        @include grayP;
        margin-top: 10px;
      }
      li {
        @include button;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 70px;
        gap: 15px;
        span {
          width: 18px;
          height: 18px;
        }
      }
    }
  }
}
